hr {
  margin-top: 10px;
  margin-bottom: 10px;
  clear: both;
}
summary {
  font-size: 20px;
  margin-top: 10px;
  margin-bottom: 10px;
}
summary:hover {
  cursor: pointer;
}
.import-fields {
  background: white;
}
.import-fields,
.import-fields-wrapper {
  position: relative;
}
.import-fields-wrapper {
  overflow: scroll;
}
.import-fields:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(241, 241, 241, 0);
  background: -moz-linear-gradient(top, rgba(241, 241, 241, 0) 0%, rgba(241, 241, 241, 0.5) 90%, #f1f1f1 100%);
  background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(241, 241, 241, 0)), color-stop(90%, rgba(241, 241, 241, 0.5)), color-stop(100%, #f1f1f1));
  background: -webkit-linear-gradient(top, rgba(241, 241, 241, 0) 0%, rgba(241, 241, 241, 0.5) 90%, #f1f1f1 100%);
  background: -o-linear-gradient(top, rgba(241, 241, 241, 0) 0%, rgba(241, 241, 241, 0.5) 90%, #f1f1f1 100%);
  background: -ms-linear-gradient(top, rgba(241, 241, 241, 0) 0%, rgba(241, 241, 241, 0.5) 90%, #f1f1f1 100%);
  background: linear-gradient(to bottom, rgba(241, 241, 241, 0) 0%, rgba(241, 241, 241, 0.5) 90%, #f1f1f1 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#0f1f1f', endColorstr='#0f1f1f', GradientType=0);
}
.step-name {
  vertical-align: middle;
}
.table-wrapper {
  overflow: scroll;
  margin-bottom: 10px;
  padding-bottom: 12px;
}
.table-wrapper table {
  table-layout: fixed;
}
.table-wrapper td {
  word-wrap: break-word;
}
.table-wrapper .cell-content {
  word-break: break-all;
}
.entry-length {
  width: 50px;
}
.preview-details {
  font-size: 15px;
  margin-top: 4px;
  margin-bottom: 4px;
}
.import-list {
  display: block;
  float: left;
  max-width: 90%;
  overflow: hidden;
  padding-right: 10px;
}
.csv-table {
  border-collapse: collapse;
  cellpadding: 2px;
  background: white;
}
tr th,
tr td,
tfoot tr {
  min-width: 130px;
  border: 1px solid black;
  text-align: left;
  border-collapse: collapse;
  padding: 3px;
}
tfoot tr td {
  colspan: "100%";
}
details ul {
  height: 300px;
  overflow: scroll;
  border: 1px solid black;
  width: 270px;
  padding: 10px;
}
summary:focus {
  outline-style: none;
}
.ifm-steps-and-vars {
  display: flex;
  width: 100%;
  clear: both;
}
.ifm-step {
  display: block;
  float: left;
  width: 100%;
  padding: 20px;
  background: white;
}
.ifm-step h4 {
  margin-top: 10px;
}
.ifm-create-steps {
  width: 50%;
  float: left;
}
.ifm-steps-wrapper {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
}
.ifm-step-wrapper {
  display: flex;
}
.ifm-step-wrapper .button-primary {
  margin-left: 5px;
}
.ifm-step .key-row,
.ifm-step .key-value {
  display: flex;
}
.step-then {
  text-align: center;
}
.ifm-map-setters {
  display: flex;
}
.ifm-step .v-select .open-indicator:before {
  border-color: rgba(60, 60, 60, 0.8);
  border-style: solid;
  border-width: 2px 2px 0 0;
}
.ifm-step .v-select .dropdown-toggle {
  background-color: white;
  font-size: 14px;
  line-height: 2;
  color: #32373c;
  border-color: #7e8993;
  box-shadow: none;
  border-radius: 3px;
  padding: 0 0 0 8px;
  min-height: 30px;
  max-width: 25rem;
  -webkit-appearance: none;
  background: #fff url(data:image/svg+xml;charset=US-ASCII,%3Csvg%20width%3D%2220%22%20height%3D%2â€¦%205-5%202%201-7%207-7-7%202-1z%22%20fill%3D%22%23555%22%2F%3E%3C%2Fsvg%3E) no-repeat right 5px top 55%;
  background-size: 16px 16px;
  cursor: pointer;
  vertical-align: middle;
}
.single-meta {
  display: flex;
  flex-direction: column;
}
.v-select .dropdown-toggle {
  background-color: white !important;
}
.v-select.dropdown {
  width: 160px;
}
.ifm-step .single-meta {
  margin-top: 5px;
  margin-bottom: 5px;
}
.key-row {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 4px;
}
.meta-key-input {
  height: 30px;
}
.ifm-var-builder {
  width: 48%;
  float: right;
}
.ifm-var-builder summary,
.ifm-create-steps summary {
  background: white;
  margin: 0;
  padding: 20px;
  border: 1px solid black;
}
.ifm-create-steps summary {
  border-bottom: none;
}
.ifm-custom-var {
  padding: 20px;
  background: white;
  border-left: 1px solid black;
  border-right: 1px solid black;
  border-bottom: none;
}
.ifm-var-builder .ifm-custom-var:nth-child(even),
.ifm-steps-wrapper .ifm-step:nth-child(even) {
  background-color: #f9f9f9;
}
.ifm-var-builder .ifm-custom-var:last-child {
  border-bottom: 1px solid black;
}
.ifm-steps-wrapper {
  border: 1px solid black;
}
.ifm-step .regular-text {
  width: 13em;
}
.ifm-step h4 {
  margin: 0;
}
.ifm-params .button-primary {
  font-size: 10px;
  height: 20px;
  min-height: 20px;
  width: 20px;
  line-height: 0px;
  text-align: center;
  padding: 0;
}
